'use client';

import { useState, useEffect } from 'react';
import { Plus, Edit, Trash2, X, Save, DollarSign } from 'lucide-react';

type MassPricing = {
  id: number;
  tipo_misa: string;
  descripcion: string;
  precio: number;
  activo: boolean;
};

const emptyPricing: Omit<MassPricing, 'id'> = {
  tipo_misa: '',
  descripcion: '',
  precio: 0,
  activo: true,
};

export default function MassPricingPage() {
  const [pricing, setPricing] = useState<MassPricing[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingPricing, setEditingPricing] = useState<MassPricing | null>(null);
  const [formData, setFormData] = useState(emptyPricing);
  const [isSaving, setIsSaving] = useState(false);

  useEffect(() => {
    fetchPricing();
  }, []);

  const fetchPricing = async () => {
    try {
      const response = await fetch('/api/admin/mass-pricing');
      const data = await response.json();
      if (data.success) {
        setPricing(data.data);
      }
    } catch (error) {
      console.error('Error al cargar precios:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const openCreateModal = () => {
    setEditingPricing(null);
    setFormData(emptyPricing);
    setIsModalOpen(true);
  };

  const openEditModal = (item: MassPricing) => {
    setEditingPricing(item);
    setFormData({
      tipo_misa: item.tipo_misa,
      descripcion: item.descripcion,
      precio: item.precio,
      activo: item.activo,
    });
    setIsModalOpen(true);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSaving(true);

    try {
      const url = editingPricing ? `/api/admin/mass-pricing/${editingPricing.id}` : '/api/admin/mass-pricing';
      const response = await fetch(url, {
        method: editingPricing ? 'PUT' : 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        fetchPricing();
        setIsModalOpen(false);
      } else {
        const error = await response.json();
        alert(error.message || 'Error al guardar');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al guardar');
    } finally {
      setIsSaving(false);
    }
  };

  const deletePricing = async (id: number) => {
    if (!confirm('¿Estás seguro de eliminar este precio?')) return;

    try {
      const response = await fetch(`/api/admin/mass-pricing/${id}`, { method: 'DELETE' });
      if (response.ok) {
        setPricing(pricing.filter(p => p.id !== id));
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const toggleActive = async (item: MassPricing) => {
    try {
      const response = await fetch(`/api/admin/mass-pricing/${item.id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ ...item, activo: !item.activo }),
      });

      if (response.ok) {
        setPricing(pricing.map(p => p.id === item.id ? { ...p, activo: !p.activo } : p));
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando precios...</p>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Precios de Misas</h1>
            <p className="text-gray-600 mt-1">Gestiona los precios de estipendios de misas</p>
          </div>
          <button onClick={openCreateModal} className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors">
            <Plus className="w-5 h-5" />
            Agregar Precio
          </button>
        </div>
      </div>

      {pricing.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {pricing.map((item) => (
            <div key={item.id} className={`bg-white rounded-lg shadow-sm overflow-hidden ${!item.activo ? 'opacity-60' : ''}`}>
              <div className="p-6">
                <div className="flex items-start justify-between mb-4">
                  <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center">
                    <DollarSign className="w-6 h-6 text-green-600" />
                  </div>
                  <button
                    onClick={() => toggleActive(item)}
                    className={`px-3 py-1 rounded-full text-xs font-medium transition-colors ${item.activo ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-600'}`}
                  >
                    {item.activo ? 'Activo' : 'Inactivo'}
                  </button>
                </div>
                
                <h3 className="text-lg font-semibold text-gray-900 mb-2">{item.tipo_misa}</h3>
                {item.descripcion && <p className="text-gray-600 text-sm mb-4">{item.descripcion}</p>}
                
                <div className="bg-gray-50 rounded-lg p-4 mb-4">
                  <p className="text-3xl font-bold text-green-600">S/ {item.precio.toFixed(2)}</p>
                </div>
                
                <div className="flex items-center justify-end gap-2">
                  <button onClick={() => openEditModal(item)} className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg transition-colors" title="Editar">
                    <Edit className="w-5 h-5" />
                  </button>
                  <button onClick={() => deletePricing(item.id)} className="p-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors" title="Eliminar">
                    <Trash2 className="w-5 h-5" />
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="bg-white rounded-lg shadow-sm p-12 text-center">
          <DollarSign className="w-16 h-16 text-gray-400 mx-auto mb-4" />
          <h3 className="text-lg font-semibold text-gray-900 mb-2">No hay precios</h3>
          <p className="text-gray-600 mb-4">Agrega el primer precio de misa</p>
          <button onClick={openCreateModal} className="inline-flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">
            <Plus className="w-5 h-5" />
            Agregar Precio
          </button>
        </div>
      )}

      {/* Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-md w-full">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-bold text-gray-900">{editingPricing ? 'Editar Precio' : 'Agregar Precio'}</h2>
                <button onClick={() => setIsModalOpen(false)} className="p-2 text-gray-400 hover:text-gray-600 transition-colors">
                  <X className="w-6 h-6" />
                </button>
              </div>
            </div>

            <form onSubmit={handleSubmit}>
              <div className="p-6 space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Tipo de Misa *</label>
                  <input
                    type="text"
                    value={formData.tipo_misa}
                    onChange={(e) => setFormData({ ...formData, tipo_misa: e.target.value })}
                    required
                    placeholder="Ej: Misa de Difuntos"
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Descripción</label>
                  <textarea
                    value={formData.descripcion}
                    onChange={(e) => setFormData({ ...formData, descripcion: e.target.value })}
                    rows={3}
                    placeholder="Descripción del tipo de misa..."
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Precio (S/) *</label>
                  <input
                    type="number"
                    value={formData.precio}
                    onChange={(e) => setFormData({ ...formData, precio: parseFloat(e.target.value) || 0 })}
                    required
                    min="0"
                    step="0.01"
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>
                <label className="flex items-center gap-2 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={formData.activo}
                    onChange={(e) => setFormData({ ...formData, activo: e.target.checked })}
                    className="w-5 h-5 rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                  />
                  <span className="text-sm font-medium text-gray-700">Precio activo</span>
                </label>
              </div>

              <div className="p-6 border-t border-gray-200 bg-gray-50 flex justify-end gap-3">
                <button type="button" onClick={() => setIsModalOpen(false)} className="px-4 py-2 text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors">
                  Cancelar
                </button>
                <button type="submit" disabled={isSaving} className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 flex items-center gap-2">
                  {isSaving ? <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div> : <Save className="w-4 h-4" />}
                  {editingPricing ? 'Actualizar' : 'Guardar'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
